@import url(https://fonts.googleapis.com/css?family=Permanent+Marker&display=swap);
@import url(https://fonts.googleapis.com/css?family=Open+Sans&display=swap);
@import url(https://fonts.googleapis.com/css?family=Space+Mono&display=swap);
@import url(https://fonts.googleapis.com/css?family=Noto+Sans:400,700);
html {
  height: 100%;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
  background-color: #cddde2;
  height: 100%;
  overflow: hidden;
}

#root {
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace;
}
.App {
  height: 100%;
}

main {
  padding: 20px 0;
  display: flex;
  align-items: center;
  justify-content: center;
  margin: auto;
  overflow: hidden;
  height: 100%;
}

.sr {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

.error {
  color: #ce0202;
  font-weight: 600;
}

.shadow {
  box-shadow: 10px 10px 13px -1px rgba(0, 0, 0, 0.09);
}

.light-shadow {
  box-shadow: 6px 5px 5px -1px rgba(0, 0, 0, 0.01);
}

#dim {
  height: 100%;
  width: 100%;
  z-index: 4;
  position: fixed;
  background-color: rgba(0, 0, 0, 0.623);
}

button:hover {
  cursor: pointer;
}

@media (max-width: 480px) {
  main {
    padding: 15px 0;
  }
}
header {
  z-index: 3;
  background-color: #5caac3;
  text-align: center;
  position: absolute;
  width: 100%;
}
header .brandIcon {
  display: inline;
  color: white;
  margin: auto;
  font-size: 2em;
}
header h1 {
  display: inline;
  margin: 0;
  padding: 0 15px;
  color: white;
  font-family: "Permanent Marker", cursive;
}

#settings {
  background-color: rgba(0, 0, 0, 0);
  border: none;
  position: absolute;
  right: 3vw;
  height: 100%;
  width: auto;
}

#settings:hover {
  cursor: pointer;
}

@media (max-width: 600px) {
  #settings {
    right: 8vw;
  }
}
#backgroundImage {
  z-index: -1;
  position: fixed;
  opacity: 0.05;
  background-repeat: repeat;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background-image: url(../../static/media/checker-480x480.f0515990.jpg);
}

.slideIconContainer {
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  font-size: 8em;
  display: flex;
}
.slideIconContainer .slideIcon {
  margin: auto;
  flex: 1 1;
}

.frame {
  z-index: 1;
  display: flex;
  flex-direction: column;
  width: 25rem;
  position: fixed;
  height: 75%;
}

.frameContents {
  z-index: 1;
  height: 100%;
  background-color: white;
  border-radius: 4px;
  padding: 10px;
  display: flex;
  flex-direction: column;
}

.petImage {
  flex: 2 1;
  padding: 10px;
  overflow: hidden;
  overflow-y: auto;
  margin: auto;
}
.petImage ul li {
  text-align: center;
}
.petImage img {
  border-radius: 3px;
  width: auto;
  max-width: 100%;
  max-height: 400px;
}

.petImage::-webkit-scrollbar {
  width: 5px;
  border-radius: 5px;
}

.petImage::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.199);
  border-radius: 5px;
}

.petImage::-webkit-scrollbar-thumb {
  border-radius: 5px;
  background-color: rgba(165, 165, 165, 0.795);
  outline: 1px solid rgba(75, 81, 87, 0);
}

.profile {
  padding: 10px;
}
.profile h2 {
  margin: 4px 0;
}
.profile h2 a {
  color: #363636;
  text-decoration: none;
}
.profile h2 a:hover {
  color: #505050;
  text-decoration: underline;
}
.profile .desc {
  margin-bottom: 10px;
}
.profile .desc p {
  display: inline;
  padding-right: 5px;
  font-style: italic;
  color: grey;
}
.controls {
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 20px;
}
.controls button {
  font-size: 2em;
  border-radius: 50%;
  border-style: none;
  padding: 0.5em;
  color: white;
}
.controls .add {
  background-color: #00b66a;
}
.controls .more {
  color: grey;
}
.controls .pass {
  background-color: #ff2f0d;
  -webkit-transform: rotate(180deg);
          transform: rotate(180deg);
}
.controls button:hover {
  cursor: pointer;
}

@media (max-width: 768px) {
  .frame {
    width: 18rem;
  }
}
@media (max-width: 480px) {
  .frame {
    height: 65%;
    width: 14rem;
  }

  .controls button {
    font-size: 1.5em;
  }

  .profile {
    padding: 2px 10px;
  }
  .profile h2 {
    font-size: 18px;
  }
  .profile .desc p {
    font-size: 12px;
  }
}
.modal {
  z-index: 4;
  position: absolute;
  display: flex;
  top: 0;
  left: 0;
  height: 100%;
  width: 100%;
  align-items: center;
  font-family: "Open Sans", sans-serif;
}

.modalBody {
  margin: auto;
  background-color: white;
  border-radius: 4px;
  max-height: 75%;
  padding: 10px;
}
.modalBody #closeModal {
  font-size: 2em;
  color: black;
  background-color: rgba(0, 0, 0, 0);
  border: none;
  text-align: right;
}
.modalBody #closeModal:hover {
  cursor: pointer;
}

.hide-modal {
  display: none;
}

#enlargedImage .modalBody {
  height: 75%;
  width: 60%;
  display: flex;
  flex-direction: column;
}
#enlargedImage .modalBody a {
  color: #5caac3;
  text-decoration: none;
}
#enlargedImage .modalBody a:hover {
  text-decoration: underline;
}
#enlargedImage .modalBody a:visited {
  color: #5c89c3;
}
#enlargedImage .modalBody h2,
#enlargedImage .modalBody h3 {
  color: #363636;
}
#enlargedImage .modalBody .profile {
  flex: 1 1;
  overflow: hidden;
  display: flex;
}
#enlargedImage .modalBody .profile .profileImage {
  flex: 1 1;
  justify-content: center;
  height: 100%;
  margin: auto;
  align-items: center;
  display: flex;
  padding: 10px;
}
#enlargedImage .modalBody .profile .profileImage img {
  margin: auto;
  max-width: 100%;
  max-height: 100%;
}
#enlargedImage .modalBody .profile .bio {
  flex: 1 1;
  padding: 10px;
  overflow: scroll;
  overflow-x: hidden;
  overflow-y: auto;
}
#enlargedImage .modalBody .profile .bio h2 {
  font-size: 2em;
}
#enlargedImage .modalBody .profile .bio p {
  margin: 4px 0;
  width: 100%;
  color: #4e4e4e;
}
#enlargedImage .modalBody .profile .bio dl {
  margin-left: 20px;
}
#enlargedImage .modalBody .profile .bio dl dt {
  font-weight: 600;
}
#enlargedImage .modalBody .profile .bio dl dd {
  margin: 0 10px 0 5px;
}
#enlargedImage .modalBody .profile .bio dl span {
  display: block;
  margin: 4px 0;
}
#enlargedImage .modalBody .profile .bio dl dt,
#enlargedImage .modalBody .profile .bio dl dd {
  display: inline;
  color: #575757;
}

@media (max-width: 950px) {
  #enlargedImage .modalBody {
    width: 80%;
  }
  #enlargedImage .modalBody .profile {
    flex-direction: column;
  }
  #enlargedImage .modalBody .profile .bio p {
    margin: 4px 0;
    width: 100%;
  }
  #enlargedImage .modalBody .profile .bio dl {
    margin-left: 5px;
  }
}
@media (max-width: 768px) {
  .modalBody #closeModal {
    font-size: 1.25em;
  }

  #enlargedImage .modalBody a {
    font-size: 21px;
  }
  #enlargedImage .modalBody .profile .bio dl {
    font-size: 12px;
  }
  #enlargedImage .modalBody .profile .bio h2, #enlargedImage .modalBody .profile .bio h3 {
    font-size: 14px;
  }
}
footer {
  color: grey;
  text-align: center;
  font-family: "Space Mono", monospace;
  font-size: 12px;
  position: absolute;
  bottom: 0;
  width: 100%;
}
.cornerRibbon {
  /* The ribbons */
  /* Custom styles */
  /* Different positions */
  /* Colors */
}
.cornerRibbon .corner-ribbon {
  font-size: 16px;
  width: 260px;
  background: #e43;
  position: absolute;
  top: 20px;
  left: -45px;
  text-align: center;
  line-height: 50px;
  letter-spacing: 1px;
  color: #000000;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.cornerRibbon .corner-ribbon.sticky {
  position: fixed;
}
.cornerRibbon .corner-ribbon.shadow {
  box-shadow: 0 0 3px rgba(0, 0, 0, 0.3);
}
.cornerRibbon .corner-ribbon.top-left {
  top: 25px;
  left: -50px;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.cornerRibbon .corner-ribbon.top-right {
  top: 25px;
  right: -50px;
  left: auto;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.cornerRibbon .corner-ribbon.bottom-left {
  top: auto;
  bottom: 25px;
  left: -50px;
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
}
.cornerRibbon .corner-ribbon.bottom-right {
  top: auto;
  right: -60px;
  bottom: 45px;
  left: auto;
  transform: rotate(-45deg);
  -webkit-transform: rotate(-45deg);
}
.cornerRibbon .corner-ribbon.white {
  background: #f0f0f0;
  color: #555;
}
.cornerRibbon .corner-ribbon.black {
  background: #333;
}
.cornerRibbon .corner-ribbon.grey {
  background: #999;
}
.cornerRibbon .corner-ribbon.blue {
  background: #39d;
}
.cornerRibbon .corner-ribbon.green {
  background: #2c7;
}
.cornerRibbon .corner-ribbon.turquoise {
  background: #1b9;
}
.cornerRibbon .corner-ribbon.purple {
  background: #95b;
}
.cornerRibbon .corner-ribbon.red {
  background: #e43;
}
.cornerRibbon .corner-ribbon.orange {
  background: #e82;
}
.cornerRibbon .corner-ribbon.yellow {
  background: #ec0;
}

@media (max-width: 480px) {
  .cornerRibbon {
    display: none;
  }
}
#sideMenu {
  position: absolute;
  z-index: 4;
  background-color: white;
  padding: 5px 20px;
  height: 100%;
  width: 100%;
  max-width: 25%;
  right: 0;
}

.hide {
  display: none;
}

.sideHeader h2 {
  display: inline;
}
.sideHeader button {
  display: inline;
  right: 0;
  position: absolute;
  border: none;
  background-color: rgba(0, 0, 0, 0);
  font-size: 25px;
  padding: 5px 10px;
}

ul {
  list-style: none;
  padding: 0;
}
ul li {
  padding: 5px 0;
}

#locationContainer {
  display: flex;
}
#locationContainer input {
  flex: 2 1;
  margin: 0 2px;
}
#locationContainer button {
  font-size: 24px;
  background: none;
  border: none;
}

input {
  border-radius: 5px;
  border: none;
  padding: 5px;
  background-color: #e6e6e6;
}

input::-webkit-input-placeholder {
  font-style: italic;
  opacity: 0.7;
}

input::-moz-placeholder {
  font-style: italic;
  opacity: 0.7;
}

input:-ms-input-placeholder {
  font-style: italic;
  opacity: 0.7;
}

input::-ms-input-placeholder {
  font-style: italic;
  opacity: 0.7;
}

input::placeholder {
  font-style: italic;
  opacity: 0.7;
}

#watchList {
  width: 100%;
  padding: 5px;
  font-weight: 600;
  color: white;
  font-size: 16px;
  background-color: #5caac3;
  border-radius: 5px;
  border: none;
}

#map {
  width: 100%;
  height: 250px;
  border-style: solid;
  border-color: #dadada;
  border-radius: 5px;
}

@media (max-width: 1280px) {
  #sideMenu {
    max-width: 45%;
  }
}
@media (max-width: 768px) {
  #sideMenu {
    max-width: 50%;
  }

  #locationContainer {
    display: block;
  }
}
@media (max-width: 500px) {
  #sideMenu {
    max-width: 75%;
  }
}
